.carousel {
    position: relative;
    &__slide {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        width: 100%;
        height: 100%;
        overflow: hidden;
        &::after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: 0;
            width: 100%;
            height: 389px;
            background: linear-gradient(180deg, #131313 0%, rgba(19, 19, 19, 0) 100%);
            transform: rotate(-180deg);
        }
    }
    &__image {
        display: block;
        width: 100%;
        border-radius: 60px 60px 0 0;
    }
    &__inner {
        position: absolute;
        z-index: 1;
        margin-bottom: 170px;
    }
    &__title {

    }
    &__subtitle {
        font-size: 42px;
        line-height: 51px;
        text-transform: uppercase;
    }
    &__tizer {
        margin: 40px 0;
    }
	&__slide {
		padding: 0;
	}
    &__button {
        margin-left: 10px;
    }
}

.toggle {
    position: absolute;
    right: 0;
    bottom: 158px;
    z-index: 2;
    &__list {
        @include reset-list;
        font-size: 14px;
        font-weight: bold;
        text-align: right;
    }
    &__item {
        text-transform: uppercase;
        opacity: 0.6;
        margin-bottom: 10px;
        cursor: pointer;
        &.active {
            opacity: 1;
        }
    }
}
