.menu {
    margin: 0 -21px;
    &__list {
        @include reset-list;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
    }
    &__item {
        margin: 0 21px;
    }
    &__link {
        text-decoration: none;
        font-size: 14px;
        line-height: 21px;
        text-transform: uppercase;
        font-weight: bold;
        color: $color-white;
        transition: color .2s linear;
        &:hover {
            color: #BFECFF;
        }
    }
    &.open {
        position: absolute;
        top: 108px;
        left: 0;
        z-index: 2;
        display: block;
        width: 100%;
        height: 100%;
        background-color: $color-main;
        padding: 55px 69px 0;
        margin: 0;
    }
    &__group-wrapper {
        display: none;
    }
}