.button {
    @include reset-button;
    position: relative;
    z-index: 1;
    width: 264px;
    height: 60px;
    font-size: 12px;
    line-height: 18px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    margin-right: 19px;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -10px;
        width: 100%;
        height: 100%;
        transform-origin: 0 100%;
        background: $color-gold;
        z-index: -1;
        transform: skew(-17deg);
        transition: background-color .2s linear;
    }
    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: -10px;
        width: 6px;
        height: 100%;
        background-color: $color-white;
        transform: skew(-17deg);
    }
    &:hover::before {
        background-color: #E3CA9C;
    }
}