@import "_variables";
@import "_mixins";
@import "_keyframes";
@import "_fonts";

@import "blocks/header";
@import "blocks/menu";
@import "blocks/button";
@import "blocks/carousel";
@import "blocks/cards";
@import "blocks/form";


html, body {
	font-family: 'Montserrat', sans-serif;
	margin: 0;
	padding: 0;
	text-size-adjust: none;
	font-size: 16px;
	line-height: 26px;
	background-color: $color-main;
	color: $color-white;
	position: relative;
}
* {
	box-sizing: border-box;
}
h1, h2 {
	margin: 0;
}
img {
	border: none;
	max-width: 100%;
}
button {
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	&:focus,
	&:active{
		outline: 0px solid transparent;
	}
}
picture {
	display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

.container {
	width: 100%;
	min-width: 320px;
	max-width: 1920px;
	padding: 0 100px;
	margin: 0 auto;	
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh) * 100);
	display: flex;
	flex-direction: column;
}

.content {
	position: relative;
	z-index: 1;
	flex-grow: 1;
	&__section {
		margin-top: 48px;
	}
}

.title {
	font-size: 42px;
	line-height: 51px;
	text-transform: uppercase;
	color: $color-white;
}

.button-icon {
	@include reset-link;
	width: 74px;
	height: 60px;
	border: 1px solid $color-white;
	opacity: 0.4;
	transform: skewX(-17deg);
	transition: opacity .8s ease;
	&::before {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		content: '';
		mask-image: url(../images/phone.svg);
		mask-repeat: no-repeat;
		mask-position: center;
		transform: skewX(17deg);
		transition: background-color .8s ease;
		background-color: #fff;
	}
	&:hover {
		opacity: 1;
	}
}

.fancybox {
	&__content {
		color: $color-white;
		background-color: transparent;
		& :focus:not(.carousel__button.is-close) {
			outline: none;
		}
	}
}

.carousel {
	&__button.is-close {
		display: none;
	}
	&__slide {
		padding: 0;
	}
}


@import "_response";