.cards {
    margin-top: 60px;
    margin-bottom: 256px;
    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        font-size: 14px;
        color: $color-black;
        margin: 38px -15px 45px;
    }
    &__item {
        align-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        max-width: 320px;
        background-color: $color-white;
        margin: 15px 15px;
    }
    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        padding: 0 24px 30px;
    }
    &__text-block {

    }
    &__image {
        display: block;
    }
    &__title {
        font-family: 'Mulish', sans-serif;
        text-transform: uppercase;
        font-size: 14px;
        margin: 25px 0 21px;
    }
    &__subtitle {
        font-size: 16px;
        margin-bottom: 20px;
    }
    &__text{
        line-height: 24px;
        color: #666666;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 4;
        overflow: hidden;
    }
    &__copyright {
        font-size: 12px;
        line-height: 24px;
        color: #666666;
    }
    &__link {
        @include reset-link;
        font-family: 'Mulish', sans-serif;
        display: block;
        width: 100%;
        text-align: center;
        color: $color-white;
        background-color: $color-black;
        padding: 12px 0;
        margin-top: 14px;
    }
    &__button {
        position: relative;
        right: 5px;
        display: block;
        margin: 0 auto;
    }
}