@media (max-width: 1680px) {
    .header__link {
        margin-right: 66px;
    }
    .menu {
        margin: 0 -15px;
        &__item {
            margin: 0 15px;
        }
    }
    .cards {
        &__item {
            margin: 10px;
        }
        &__inner {
            padding: 0 20px 20px;
        }
        &__title {
            line-height: 22px;
            margin: 20px 0 16px;
        }
        &__subtitle {
            line-height: 22px;
            margin-bottom: 16px;
        }
        &__text {
            line-height: 20px;
        }
    }
    .form {
        &__inner {
            padding: 56px 46px 138px 70px;
        }
        &__close {
            right: 36px;
        }
    }
}

@media (max-width: 1400px) {
    .button {
        width: 234px;
    }
    .header {
        flex-wrap: wrap;
        justify-content: space-between;
        &__phone {
            margin-right: 22px;
        }
    }
    .form {
        &__close {
            top: 40px;
        }
        &__submit-wrapper {
            display: block;
            margin-left: 0;
        }
        &__button {
            margin: 0;
        }
        &__agreement {
            margin-top: 16px;
        }
        &__text {
            margin: 20px 0 40px;
        }
    }
}

@media (max-width: 1200px) {
    .container {
        padding: 0 48px;
    }
    .hamburger {
        display: block;
    }
    .header {
        &__menu {
            display: none;
        }
        &__link {
            order: 2;
            margin-right: 0;
        }
        &__group-wrapper {
            order: 3;
            margin-left: 0;
        }
        &__phone {
            margin-right: 7px;
        }
    }
    .menu {
        &__list {
            display: block;
        }
        &__item {
            margin: 0 0 15px;
        }
        &__link {
            font-size: 36px;
            line-height: 54px;
            text-shadow: 1px 0 1px #fff, 0 1px 1px #fff, -1px 0 1px #fff, 0 -1px 1px #fff;
            color: #131313;
            opacity: 0.4;
            &:hover {
                color: #fff;
            }
        }
    }
    .button {
        width: 183px;
    }
    .carousel {
        &__inner {
            width: 75%;
            margin-bottom: 70px;
        }
        &__tizer {
            margin: 25px 0;
        }
    }
    .title {
        font-size: 36px;
        line-height: 45px;
    }
    .toggle {
        bottom: 70px;
    }
    .fancybox {
        &__slide {
            padding: 0;
        }
    }
    .form {
        &__image {
            display: none;
        }
        &__inner {
            padding: 56px 46px;
            background: #131313;
            &::before {
                display: none;
            }
        }
        &__close {
            width: 76px;
            height: 60px;
        }
    }
}

@media (max-width: 1024px) {
    .fancybox__slide {
        padding: 0;
    }
}

@media (max-width: 768px) {
    .header {
        &__group-wrapper {
            display: none;
        }
    }
    .menu {
        &__group-wrapper {
            display: block;
        }
        &__button {
            width: 100%;
            &::after {
                display: none;
            }
        }
        &__phone {
            display: block;
            width: 100%;
            height: 60px;
            margin: 51px 0 6px;
        }
    }
    .title {
        font-size: 26px;
        line-height: 30px;
    }
    .toggle {
        position: relative;
        margin-top: 100px;
        &__list {
            text-align: left;
            margin: 0 -5px;
        }
        &__item {
            display: inline-block;
            font-size: 12px;
            margin: 0 5px;
        }
    }
    .carousel {
        &__inner {
            width: 100%;
        }
        &__tizer {
            margin: 20px 0;
        }
    }
    .cards__item {
        margin: 10px auto;
    }
}

@media (max-width: 576px) {
    .carousel {
        &__inner {
            margin-bottom: 10px;
        }
    }
    .cards {
        margin-top: 0;
    }
    .form {
        padding: 0;
    }
}
@media (max-width: 480px) {
    .container {
        padding: 0 30px;
    }
    .carousel {
        &__image {
            display: none;
        }
        &__inner {
            position: relative;
            margin-top: 40px;
        }
        &__slide {
            &::after {
                display: none;
            }
        }
    }
    .menu {
        &__link {
            font-size: 26px;
            line-height: 40px;
        }
        &.open {
            padding: 55px 30px 0;
        }
    }
    .form {
        &__inner {
            padding: 96px 20px 56px;
        }
        &__close {
            top: 20px;
        }
    }
    .error-text {
        line-height: 20px;
        font-size: 8px;
    }
}