.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 28px 0 20px;
  margin-bottom: 10px;
}

.header__link {
  margin-right: 76px;
}

.header__image {
  display: block;
}

.header__group-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: auto;
}

.header__phone {
  margin-right: 32px;
}

.hamburger {
  z-index: 2;
  display: none;
  order: 1;
  width: 37px;
  height: 31px;
  padding: 6px;
  cursor: pointer;
}

.hamburger__line {
  height: 3px;
  border-radius: 3px;
  background: #fff;
  margin-bottom: 5px;
  transition: all .5s ease;
}

.hamburger__line:nth-child(1) {
  width: 16px;
}

.hamburger__line:nth-child(2) {
  width: 31px;
}

.hamburger__line:nth-child(3) {
  width: 22px;
  margin-bottom: 0;
}

.hamburger.active .hamburger__line:nth-child(1) {
  width: 31px;
  transform: translateY(8px) rotate(-45deg);
}

.hamburger.active .hamburger__line:nth-child(2) {
  background: transparent;
  opacity: 0;
}

.hamburger.active .hamburger__line:nth-child(3) {
  width: 31px;
  transform: translateY(-8px) rotate(45deg);
}

.hamburger.active .header.mobile .header__menu {
  display: block;
}

.menu {
  margin: 0 -21px;
}

.menu__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.menu__item {
  margin: 0 21px;
}

.menu__link {
  text-decoration: none;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
  transition: color .2s linear;
}

.menu__link:hover {
  color: #BFECFF;
}

.menu.open {
  position: absolute;
  top: 108px;
  left: 0;
  z-index: 2;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #131313;
  padding: 55px 69px 0;
  margin: 0;
}

.menu__group-wrapper {
  display: none;
}

.button {
  border: none;
  background-color: transparent;
  outline: 0px solid transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
  font-size: inherit;
  color: inherit;
  font: inherit;
  border-radius: 0;
  position: relative;
  z-index: 1;
  width: 264px;
  height: 60px;
  font-size: 12px;
  line-height: 18px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  margin-right: 19px;
}

.button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -10px;
  width: 100%;
  height: 100%;
  transform-origin: 0 100%;
  background: #C2AB81;
  z-index: -1;
  transform: skew(-17deg);
  transition: background-color .2s linear;
}

.button::after {
  content: '';
  position: absolute;
  top: 0;
  right: -10px;
  width: 6px;
  height: 100%;
  background-color: #fff;
  transform: skew(-17deg);
}

.button:hover::before {
  background-color: #E3CA9C;
}

.carousel {
  position: relative;
}

.carousel__slide {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.carousel__slide::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 0;
  width: 100%;
  height: 389px;
  background: linear-gradient(180deg, #131313 0%, rgba(19, 19, 19, 0) 100%);
  transform: rotate(-180deg);
}

.carousel__image {
  display: block;
  width: 100%;
  border-radius: 60px 60px 0 0;
}

.carousel__inner {
  position: absolute;
  z-index: 1;
  margin-bottom: 170px;
}

.carousel__subtitle {
  font-size: 42px;
  line-height: 51px;
  text-transform: uppercase;
}

.carousel__tizer {
  margin: 40px 0;
}

.carousel__slide {
  padding: 0;
}

.carousel__button {
  margin-left: 10px;
}

.toggle {
  position: absolute;
  right: 0;
  bottom: 158px;
  z-index: 2;
}

.toggle__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  font-size: 14px;
  font-weight: bold;
  text-align: right;
}

.toggle__item {
  text-transform: uppercase;
  opacity: 0.6;
  margin-bottom: 10px;
  cursor: pointer;
}

.toggle__item.active {
  opacity: 1;
}

.cards {
  margin-top: 60px;
  margin-bottom: 256px;
}

.cards__list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  font-size: 14px;
  color: #171717;
  margin: 38px -15px 45px;
}

.cards__item {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 320px;
  background-color: #fff;
  margin: 15px 15px;
}

.cards__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 0 24px 30px;
}

.cards__image {
  display: block;
}

.cards__title {
  font-family: 'Mulish', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  margin: 25px 0 21px;
}

.cards__subtitle {
  font-size: 16px;
  margin-bottom: 20px;
}

.cards__text {
  line-height: 24px;
  color: #666666;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
}

.cards__copyright {
  font-size: 12px;
  line-height: 24px;
  color: #666666;
}

.cards__link {
  text-decoration: none;
  color: inherit;
  outline: none;
  font-family: 'Mulish', sans-serif;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  background-color: #171717;
  padding: 12px 0;
  margin-top: 14px;
}

.cards__button {
  position: relative;
  right: 5px;
  display: block;
  margin: 0 auto;
}

.form-wrapper {
  display: none;
}

.form {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.form__image {
  width: 819px;
}

.form__image:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-image: url("images/bg-modal.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transform-origin: 0 100%;
  transform: skew(-3deg);
}

.form__inner {
  position: relative;
  width: 1000px;
  padding: 56px 46px 138px 139px;
}

.form__inner:before {
  content: '';
  position: absolute;
  top: 0;
  left: -10px;
  width: 100%;
  height: 100%;
  transform-origin: 0 100%;
  background: #131313;
  z-index: -1;
  transform: skew(-3deg);
}

.form__title {
  font-weight: bold;
}

.form__text {
  font-size: 14px;
  line-height: 21px;
  color: #808080;
  margin: 20px 0 70px;
}

.form__close {
  border: none;
  background-color: transparent;
  outline: 0px solid transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
  font-size: inherit;
  color: inherit;
  font: inherit;
  border-radius: 0;
  position: absolute;
  top: 55px;
  right: 61px;
  width: 86px;
  height: 70px;
  border: 1px solid #fff;
  opacity: 0.4;
  transform: skewX(-17deg);
}

.form__close::before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-image: url("../images/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  transform: skewX(17deg);
}

.form__group {
  position: relative;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 20px;
}

.form__label {
  position: absolute;
  top: 21px;
  left: 36px;
  transition: all .2s linear;
}

.form__input {
  border: none;
  background-color: transparent;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: inherit;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  border-radius: 0;
  appearance: none;
  width: 100%;
  height: 70px;
  font-weight: bold;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 33px 36px 17px;
}

.form__input::placeholder {
  opacity: 1;
  color: inherit;
}

.form__input:focus::placeholder {
  opacity: 0;
}

.form__input.active {
  border: 1px solid #fff;
}

.form__input.active + .form__label {
  top: 15px;
  font-size: 12px;
  line-height: 15px;
  opacity: 0.4;
}

.form__input.error {
  border: 1px solid #FE60B7;
  box-shadow: 0 0 2px 1px #FE60B7;
}

.form__textarea {
  resize: vertical;
  outline: none;
  width: 100%;
  height: 150px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  color: #fff;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 33px 36px 17px;
}

.form__textarea.active {
  border: 1px solid #fff;
}

.form__textarea.active + .form__label {
  top: 15px;
  font-size: 12px;
  line-height: 15px;
  opacity: 0.4;
}

.form__textarea.error {
  border: 1px solid #FE60B7;
  box-shadow: 0 0 2px 1px #FE60B7;
}

.form__submit-wrapper {
  display: flex;
  margin-left: 11px;
  margin-top: -4px;
}

.form__button {
  margin: 0 50px 0 0;
}

.form__agreement {
  font-size: 14px;
  line-height: 21px;
  color: #808080;
  margin: 0;
}

.form__link {
  text-decoration: none;
  color: inherit;
  outline: none;
  text-decoration: underline;
}

.error-text {
  position: absolute;
  bottom: 0;
  right: 10px;
  font-size: 12px;
  text-transform: lowercase;
  color: #FE60B7;
}

html, body {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  text-size-adjust: none;
  font-size: 16px;
  line-height: 26px;
  background-color: #131313;
  color: #fff;
  position: relative;
}

* {
  box-sizing: border-box;
}

h1, h2 {
  margin: 0;
}

img {
  border: none;
  max-width: 100%;
}

button {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button:focus, button:active {
  outline: 0px solid transparent;
}

picture {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1920px;
  padding: 0 100px;
  margin: 0 auto;
}

.wrapper {
  min-height: 100vh;
  min-height: calc(var(--vh) * 100);
  display: flex;
  flex-direction: column;
}

.content {
  position: relative;
  z-index: 1;
  flex-grow: 1;
}

.content__section {
  margin-top: 48px;
}

.title {
  font-size: 42px;
  line-height: 51px;
  text-transform: uppercase;
  color: #fff;
}

.button-icon {
  text-decoration: none;
  color: inherit;
  outline: none;
  width: 74px;
  height: 60px;
  border: 1px solid #fff;
  opacity: 0.4;
  transform: skewX(-17deg);
  transition: opacity .8s ease;
}

.button-icon::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  content: '';
  mask-image: url(../images/phone.svg);
  mask-repeat: no-repeat;
  mask-position: center;
  transform: skewX(17deg);
  transition: background-color .8s ease;
  background-color: #fff;
}

.button-icon:hover {
  opacity: 1;
}

.fancybox__content {
  color: #fff;
  background-color: transparent;
}

.fancybox__content :focus:not(.carousel__button.is-close) {
  outline: none;
}

.carousel__button.is-close {
  display: none;
}

.carousel__slide {
  padding: 0;
}

@media (max-width: 1680px) {
  .header__link {
    margin-right: 66px;
  }
  .menu {
    margin: 0 -15px;
  }
  .menu__item {
    margin: 0 15px;
  }
  .cards__item {
    margin: 10px;
  }
  .cards__inner {
    padding: 0 20px 20px;
  }
  .cards__title {
    line-height: 22px;
    margin: 20px 0 16px;
  }
  .cards__subtitle {
    line-height: 22px;
    margin-bottom: 16px;
  }
  .cards__text {
    line-height: 20px;
  }
  .form__inner {
    padding: 56px 46px 138px 70px;
  }
  .form__close {
    right: 36px;
  }
}

@media (max-width: 1400px) {
  .button {
    width: 234px;
  }
  .header {
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .header__phone {
    margin-right: 22px;
  }
  .form__close {
    top: 40px;
  }
  .form__submit-wrapper {
    display: block;
    margin-left: 0;
  }
  .form__button {
    margin: 0;
  }
  .form__agreement {
    margin-top: 16px;
  }
  .form__text {
    margin: 20px 0 40px;
  }
}

@media (max-width: 1200px) {
  .container {
    padding: 0 48px;
  }
  .hamburger {
    display: block;
  }
  .header__menu {
    display: none;
  }
  .header__link {
    order: 2;
    margin-right: 0;
  }
  .header__group-wrapper {
    order: 3;
    margin-left: 0;
  }
  .header__phone {
    margin-right: 7px;
  }
  .menu__list {
    display: block;
  }
  .menu__item {
    margin: 0 0 15px;
  }
  .menu__link {
    font-size: 36px;
    line-height: 54px;
    text-shadow: 1px 0 1px #fff, 0 1px 1px #fff, -1px 0 1px #fff, 0 -1px 1px #fff;
    color: #131313;
    opacity: 0.4;
  }
  .menu__link:hover {
    color: #fff;
  }
  .button {
    width: 183px;
  }
  .carousel__inner {
    width: 75%;
    margin-bottom: 70px;
  }
  .carousel__tizer {
    margin: 25px 0;
  }
  .title {
    font-size: 36px;
    line-height: 45px;
  }
  .toggle {
    bottom: 70px;
  }
  .fancybox__slide {
    padding: 0;
  }
  .form__image {
    display: none;
  }
  .form__inner {
    padding: 56px 46px;
    background: #131313;
  }
  .form__inner::before {
    display: none;
  }
  .form__close {
    width: 76px;
    height: 60px;
  }
}

@media (max-width: 1024px) {
  .fancybox__slide {
    padding: 0;
  }
}

@media (max-width: 768px) {
  .header__group-wrapper {
    display: none;
  }
  .menu__group-wrapper {
    display: block;
  }
  .menu__button {
    width: 100%;
  }
  .menu__button::after {
    display: none;
  }
  .menu__phone {
    display: block;
    width: 100%;
    height: 60px;
    margin: 51px 0 6px;
  }
  .title {
    font-size: 26px;
    line-height: 30px;
  }
  .toggle {
    position: relative;
    margin-top: 100px;
  }
  .toggle__list {
    text-align: left;
    margin: 0 -5px;
  }
  .toggle__item {
    display: inline-block;
    font-size: 12px;
    margin: 0 5px;
  }
  .carousel__inner {
    width: 100%;
  }
  .carousel__tizer {
    margin: 20px 0;
  }
  .cards__item {
    margin: 10px auto;
  }
}

@media (max-width: 576px) {
  .carousel__inner {
    margin-bottom: 10px;
  }
  .cards {
    margin-top: 0;
  }
  .form {
    padding: 0;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 30px;
  }
  .carousel__image {
    display: none;
  }
  .carousel__inner {
    position: relative;
    margin-top: 40px;
  }
  .carousel__slide::after {
    display: none;
  }
  .menu__link {
    font-size: 26px;
    line-height: 40px;
  }
  .menu.open {
    padding: 55px 30px 0;
  }
  .form__inner {
    padding: 96px 20px 56px;
  }
  .form__close {
    top: 20px;
  }
  .error-text {
    line-height: 20px;
    font-size: 8px;
  }
}
