.form-wrapper {
    display: none;
}
.form {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    width: 100%;
    &__image {
        width: 819px;
        &:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background-image: url('images/bg-modal.jpg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            transform-origin: 0 100%;
            transform: skew(-3deg);
        }
    }
    &__inner {
        position: relative;
        width: 1000px;
        padding: 56px 46px 138px 139px;
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: -10px;
            width: 100%;
            height: 100%;
            transform-origin: 0 100%;
            background: #131313;
            z-index: -1;
            transform: skew(-3deg);
        }
    }
    &__title {
        font-weight: bold;
    }
    &__text {
        font-size: 14px;
        line-height: 21px;
        color: #808080;
        margin: 20px 0 70px;
    }
    &__close {
        @include reset-button;
        position: absolute;
        top: 55px;
        right: 61px;
        width: 86px;
        height: 70px;
        border: 1px solid $color-white;
        opacity: 0.4;
        transform: skewX(-17deg);
        &::before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background-image: url('../images/close.svg');
            background-repeat: no-repeat;
            background-position: center;
            transform: skewX(17deg);
        }
    }
    &__group {
        position: relative;
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 20px;
    }
    &__label {
        position: absolute;
        top: 21px;
        left: 36px;
        transition: all .2s linear;
    }
    &__input {
        @include reset-input;
        width: 100%;
        height: 70px;
        font-weight: bold;
        background: transparent;
        border: 1px solid rgba(255, 255, 255, 0.4);
        padding: 33px 36px 17px;
        &.active {
            border: 1px solid $color-white;
            & + .form__label {
                top: 15px;
                font-size: 12px;
                line-height: 15px;
                opacity: 0.4;
            }
        }
        &.error {
            border: 1px solid #FE60B7;
            box-shadow: 0 0 2px 1px #FE60B7;
        }
    }
    &__textarea {
        resize: vertical;
        outline: none;
        width: 100%;
        height: 150px;
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        color: $color-white;
        background: transparent;
        border: 1px solid rgba(255, 255, 255, 0.4);
        padding: 33px 36px 17px;
        &.active {
            border: 1px solid $color-white;
            & + .form__label {
                top: 15px;
                font-size: 12px;
                line-height: 15px;
                opacity: 0.4;
            }
        }
        &.error {
            border: 1px solid #FE60B7;
            box-shadow: 0 0 2px 1px #FE60B7;
        }
    }
    &__submit-wrapper {
        display: flex;
        margin-left: 11px;
        margin-top: -4px;
    }
    &__button {
        margin: 0 50px 0 0;
    }
    &__agreement {
        font-size: 14px;
        line-height: 21px;
        color: #808080;
        margin: 0;
    }
    &__link {
        @include reset-link;
        text-decoration: underline;
    }
}

.error-text {
    position: absolute;
    bottom: 0;
    right: 10px;
    font-size: 12px;
    text-transform: lowercase;
    color: #FE60B7;
}